import { Injectable } from '@angular/core';
import { Http, Headers } from '@angular/http';
import { HttpClient } from '../../_common/http-client';
import { Observable } from 'rxjs';

@Injectable()
export class BloggerService {
  constructor(public http: HttpClient) {
  }

  /**
   * [postBlogger ]
   * @param  {[Object]} model []
   * @return {[Observable]}   []
   */
  postBlogger(model){
    const name = model.name;
    const url = model.url;
    const taskjs  = model.taskjs;
    let creds = 'name=' + name + '&url=' + url + '&taskjs=' + taskjs;
    let headers = new Headers();
    headers.append('Content-Type', 'application/x-www-form-urlencoded');
    return this.http.post('http://115.29.51.196:3001/bloggers', creds, {
      headers: headers
    }).map(res => res.json());
  }
  /**
   * Token
   */
  addBlogger() {
    const name = 'yinwang';
    const url = 'http://yinwang.org';
    const taskjs  = 'yinwang.org.js';
    let creds = 'name=' + name + '&url=' + url + '&taskjs=' + taskjs;
    let headers = new Headers();
    headers.append('Content-Type', 'application/x-www-form-urlencoded');
    return this.http.post('http://115.29.51.196:3001/bloggers', creds, {
      headers: headers
    }).map(res => res.json());
  }

  /**
   * 
   */
  getBloggers() {
    return this.http.get('http://115.29.51.196:3001/bloggers').map(res => res.json());
  }

   /**
   * 
   */
  getBloggerNews() {
    return this.http.get('http://115.29.51.196:3001/blognews').map(res => res.json());
  }
}



